import { Injectable } from "@angular/core";
import { BehaviorSubject } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class LoaderService {
  isLoaderEnable: BehaviorSubject<boolean>;
  private commingState: boolean;

  constructor() {
    this.isLoaderEnable = new BehaviorSubject<boolean>(false);
  }

  private setLoader(commingState: boolean) {
    this.commingState = commingState;
    // Need a little timeout to pass angular hook and avoid afterview check exception
    setTimeout(() => {
      if (this.commingState !== this.isLoaderEnable.getValue()) {
        this.isLoaderEnable.next(commingState);
      }
    }, 500);
  }

  showLoader() {
    this.setLoader(true);
  }

  hideLoader() {
    this.setLoader(false);
  }
}
